.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 0;
  background-image: url(./../app/images/mobile/image-hero.png);
  position: relative;
  height: fit-content;
  width: 100%;
  object-fit: cover;
  background-repeat: no-repeat;
  background-size: 100%;
  overflow: hidden;
  max-height: 1200px;
}
.header__container {
  position: relative; /* Ensure itâ€™s positioned relative to the page */
  z-index: 999; /* Ensure it has a lower z-index than the menu */
  padding: 2.5rem 1.25rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.header__container--logo1 {
  z-index: 1001;
  grid-column: 3/1;
  height: 100%;
  width: 100%;
}
@media (min-width: 33.125em) {
  .header__container--logo1 {
    grid-column: 3/1;
  }
}
#menu-toggle:checked + .header__container--list {
  display: flex;
}
.header__container--hamburger {
  z-index: 1001;
  grid-column: 5;
  cursor: pointer;
}
.header__container--hamburger .line {
  display: block;
  margin-bottom: 6px;
  width: 26px;
  height: 2px;
  background-color: var(--white);
  border-radius: 2px; /* Rounded edges for a smoother look */
  transition: all 0.3s ease; /* For smooth transitions on hover/click */
}
.header__container--hamburger.active .line:nth-child(1) {
  transform: rotate(45deg) translate(5px, 6px);
}
.header__container--hamburger.active .line:nth-child(2) {
  opacity: 0;
}
.header__container--hamburger.active .line:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -6px);
}
.header__container--list {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (max-width: 43.375em) {
  .header__container--list {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: var(--black);
    padding: 2rem;
    z-index: 1000;
    overflow-y: auto;
  }
}
.header__container--list.active {
  margin: 0;
  display: flex;
  justify-content: start;
  align-items: center;
  overflow: hidden;
}
.header__big {
  align-items: center;
  padding: 1.25rem;
  margin-top: 5.5625rem;
  margin-bottom: 6.25rem;
  width: 100%;
  border: 5px solid var(--white);
  text-transform: uppercase;
  text-align: start;
}
@media (min-width: 43.4375em) {
  .header {
    background-image: url(./../app/images/desktop/image-hero.jpg);
    background-size: cover; /* Stretches the image to cover the entire area */
    background-position: center;
  }
  .header__big {
    width: 500px;
    margin-right: 150px;
  }
  .header__container {
    padding: 2.5rem 1.25rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .header__container--hamburger {
    display: none;
  }
  .header__container--list {
    grid-column: 5;
    display: flex;
    flex-direction: row;
  }
}

